
import { defineComponent, ref, onMounted, isProxy } from 'vue';
import { useRoute, useRouter } from 'vue-router';
import { ILogin } from '@/types/Interfaces';

import {formRequired} from '@/mixins/FormValidator';

import useAuth from '@/types/Auth';
import useDoctor from '@/types/Doctor';
import { remove } from 'lodash';

export default defineComponent({
  components: {
  },
  setup() {
    const router = useRouter();

    const {
      createToken,
      storeToken,
      removeToken,
      getUserId,
      getProfile,
      storeProfile,
      removeProfile,
      getProfileFromStorage
    } = useAuth('doctor');
    
    const {
      getDoctor
    } = useDoctor('doctor');

    const formData = ref<ILogin>({
      email: '',
      password: '' 
    });
    
    const form = ref<HTMLFormElement | null>(null);

    const loginError = ref<string>('');
    
    const setLoginError = (message: string) => {
      loginError.value = message;
      window.setTimeout(() => {
        loginError.value = '';
      }, 5000);  
    };
    
    onMounted(() => {
      removeToken()
    });

    const loading = ref(false);

    const onLogin = async (event: Event) => {
      // validate
      if (form.value) {
        const isValid = form.value.checkValidity();
        if (!isValid) {
          alert('not vaida')
          return;
        }
      }
      try {
        loading.value = true;
        const token = await createToken(formData.value);
        storeToken(token);
        const userId = await getUserId();
        // const profile = await getPro(userId);
        // console.log(profile)
        // storeProfile(profile)

        router.push({ name: 'DoctorDashboard' }) ;
      } catch (err) {
        console.log(err)
        if (err.response) {
          const { status, data } = err.response;
          console.log(data)
          if (status >= 400 && status < 500) {
            setLoginError('ログインに失敗しました。')
          } else {
            setLoginError(JSON.stringify(data))
          }
          // formData.value = {
          //   email: '',
          //   password: ''
          // }
        } else {
          // network error or timeout
          setLoginError('ネットワークエラー')
        }
        
      }
      loading.value = false;
      
    };
    const isPasswordHidden = ref(true);
    return {
      loading,
      form,
      formRequired,
      formData,
      loginError,
      // formValid,
      onLogin,
      isPasswordHidden
    };
  }
})
